body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'digital-7';
  src: url(/Calculator/static/media/digital-7.1e670d88.ttf);
}



#calculator {
  font-family: 'digital-7', monospace;
  width: 250px;
  height: 350px;
  margin: auto;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-areas:
      "display display display display"
      "one two three add"
      "four five six subtract"
      "seven eight nine multiply"
      "zero zero decimal divide"
      "clear clear equals equals";
  margin-top: 10vh;
  border: thick solid black;
}


#display-container { grid-area: display; }
#one { grid-area: one; }
#two { grid-area: two; }
#three { grid-area: three; }
#four { grid-area: four; }
#five { grid-area: five; }
#six { grid-area: six; }
#seven { grid-area: seven; }
#eight { grid-area: eight; }
#nine { grid-area: nine; }
#zero { grid-area: zero; }
#decimal { grid-area: decimal; }
#add { grid-area: add; }
#subtract { grid-area: subtract; }
#multiply { grid-area: multiply; }
#divide { grid-area: divide; }
#clear { grid-area: clear; }
#equals { grid-area: equals; }


#display, #expression-display {
  background-color: black;
  width: 100%;
  text-align: right;
  overflow: auto;
}

#display {
  font-size: 3em;
  color: white;
}

#expression-display {
  font-size: 2em;
  color: orange;
  height: 30px;
}

button {
  font-size: 2em;
  outline: none;
}

.digit, #decimal {
  background-color: black;
  color: white;
}

.operation {
  background-color: black;
  color: green;
  font-size: 2.5em;
}

#equals {
  background-color: black;
  color: green;
  font-size: 3em;
}

#clear {
  background-color: black;
  color: red;
}
